@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&family=Roboto+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

li,
a,
button {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 16px;
  color: #edf0f1;
  text-decoration: none;
}
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 10%;
  background-color: #24252a;
}
.logo {
  color: #edf0f1;
  font-size: 4rem !important;
}
.nav-links {
  list-style: none;
}
.nav-links li {
  display: inline-block;
  padding: 0 20px;
}

.nav-links li a {
  transition: 0.3s ease;
}
.nav-links li :hover {
  color: rgb(89, 179, 104);
}
button {
  color: crimson;
}

* {
  /* font-family: "Berkshire Swash", cursive; */
  font-family: "Roboto Condensed", sans-serif;
}
.container {
  width: 100%;
  height: calc(100vh - 80px);
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/foodWebsite/static/media/FoodPoster.d4940205.jpg);

  background-size: cover;
  display: flex;
}
.links {
  width: 20%;
  padding: 20px 20px;
  height: 100%;
  display: flex;
  align-items: center;
}
.links ul {
  list-style: none;
}
.links ul li {
  justify-content: center;
  padding: 20px 0px;
}
.content {
  width: 80%;
  height: 100%;
}

.content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.content h1 {
  font-family: "Berkshire Swash", cursive;
  font-size: 10rem;
  color: white;
  text-shadow: 0px 3px 5px rgb(61, 61, 61);
}
.content p {
  font-size: 2rem;
  color: white;
  text-align: center;
  padding: 50px 0px 50px;
}

.lines {
  width: 20%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.lines ul li {
  font-size: 1.5rem;
  padding: 20% 0;
}
.heroButton {
  width: 60px;
  height: 60px;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  transition: 0.7s;
  box-shadow: 0px 0px 15px rgb(32, 28, 28);
  outline: none;
}
.heroButton:hover {
  color: rgb(4, 44, 31);
  transform: rotate(360deg);
  box-shadow: 0px 0px 0px rgb(32, 28, 28);
}
.arrow {
  font-size: 1.5rem !important;
}

.Catcontainer {
  width: 100%;
  /* height: calc(90vh - 80px); */
  height: auto;
  background-color: rgb(44, 44, 44);
}
.catagoriesBar {
  width: 100%;
  color: lavender;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.catagoriesBar .contItem {
  display: flex;
  justify-content: flex-start;
  background-color: rgb(44, 44, 44);
  width: 100%;
  border: solid 1.5px rgb(88, 88, 88);
  padding: 20px 2%;
  font-size: 2rem;
}
.catagoriesBar :last-child {
  display: flex;
  align-items: center;
  justify-content: flex-end;

  /* position: relative;
  margin-left: 70%; */
}
input {
  height: 38px;
  outline: none;
  border: none;
  padding-left: 5px;
}
.popCatContainer {
  display: flex;
  flex-direction: column;
}
.catNav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 60px 30px;
}
.div2 {
  display: flex;
}
.catGallary {
  /* padding: 0px 20px; */
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.catGallaryShow {
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  overflow: hidden;
  display: flex;
  height: 720px;
  transition: height ease 0.5s;
}
.parent {
  overflow: hidden;
  transition: height ease 0.5s;
}

* {
  margin: 0;
  padding: 0;
}

.CatGallayCardDiv {
  width: 350px;
  height: 240px;
  margin: 20px 10px 20px 20px;
}

.imgContainer {
  position: relative;
  width: 350px;
  height: 240px;
  -webkit-filter: brightness(0.5);
          filter: brightness(0.5);
  cursor: pointer;
}
.imgContainer img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.text {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  color: rgb(255, 255, 255);
  text-shadow: 2px 2px 8px #000000;
}
.imgContainer:hover {
  transform: scale(1.1);
  -webkit-filter: brightness(1);
          filter: brightness(1);
  transition: 0.2s;
}
.keywordComponent {
  background-color: rgb(44, 44, 44);
  height: 30vh;
}
.kContainer {
  flex-direction: row;
}
.keywordComponent ul {
  padding: 50px 50px;
}
.keywordComponent ul li {
  margin: 20px;
  cursor: pointer;
  width: auto;
  display: inline-block;
}
.keywordComponent ul li:hover {
  color: rgb(5, 190, 89);
}

.recipeMainDiv {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: 50px 30px 10px;
  background-color: rgb(199, 199, 199);
}
.recipeMainDiv h1 {
  width: 100%;
  text-align: center;
}
.recipesViewAll {
  width: 100%;
  text-align: center;
  padding: 50px;
  text-decoration: underline;
  cursor: pointer;
}
.recipeContainer {
  width: 100%;
  flex-wrap: wrap;
  padding-top: 50px;
  display: flex;
  justify-content: space-around;
}
.card {
  width: 300px;
  height: 350px;
  background-color: rgb(226, 226, 226);
  margin: 20px;
  border-radius: 3px;
}
.top {
  width: 100%;
  height: 60%;
}

.top img {
  width: 100%;
  height: 100%;
}

.top p {
  font-size: 1.2rem;
  padding-left: 20px;
  transform: translate(0px, -30px);
}

.info {
  width: 100%;
  height: 40%;
}
.info h2 {
  padding: 30px 10px;
}

